.StatBlock {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.data-block {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 3px;
  border: 1px solid black;
}
.data-block .data-label{
  display: inline-block;
}
.data-block .data-value{
  display: inline-block;
}


